import React from 'react'

const AddToCart = () => {
  return (
    
      <div className="min-h-screen bg-slate-100 px-6 py-10">
  <h1 className="text-3xl font-bold mb-6 text-slate-800">Your Cart</h1>
  <div className="grid grid-cols-1 md:grid-cols-3 gap-8">
    
    {/* Left: Cart Items */}
    <div className="md:col-span-2 space-y-6">
      {cartItems.map((item) => (
        <div key={item._id} className="flex items-center bg-white rounded-xl shadow-md p-4">
          <img src={item.product.images} className="w-24 h-24 object-cover rounded-lg" />
          <div className="ml-4 flex-1">
            <h2 className="text-lg font-semibold">{item.product.name}</h2>
            <p className="text-sm text-slate-600">₹{item.product.price}</p>
            <div className="flex items-center mt-2 gap-2">
              <button className="px-2 py-1 bg-slate-200 rounded">-</button>
              <span>{item.quantity}</span>
              <button className="px-2 py-1 bg-slate-200 rounded">+</button>
            </div>
          </div>
          <div className="text-right">
            <p className="font-bold">₹{item.product.price * item.quantity}</p>
            <button className="text-red-500 text-sm mt-2">Remove</button>
          </div>
        </div>
      ))}
    </div>

    {/* Right: Summary */}
    <div className="bg-white rounded-xl shadow-md p-6 h-fit">
      <h2 className="text-xl font-bold mb-4">Order Summary</h2>
      <p className="flex justify-between">
        <span>Total Items:</span> <span>{cartItems.length}</span>
      </p>
      <p className="flex justify-between font-semibold mt-2">
        <span>Total Price:</span> <span>₹{totalPrice}</span>
      </p>
      <button className="w-full mt-6 bg-blue-600 text-white py-3 rounded-lg hover:bg-blue-700">
        Proceed to Checkout
      </button>
    </div>
  </div>
</div>

    
  )
}

export default AddToCart
